<template>
  <div class="showBox">
    <section class="grid-stack"></section>
  </div>
</template>

<script>
import { reactive, onMounted } from "vue";
import { useStore } from "vuex";
import router from "@/router";
import Chart from "chart.js/auto";
import { GridStack } from "gridstack";
import "gridstack/dist/h5/gridstack-dd-native";
export default {
  name: "ShowBox",
  props: {},
  setup() {
    const store = useStore();
    let grid = null;
    const items = reactive({ arr: [] }); //目前有創建的box data
    const chart_item = reactive({ arr: [] }); //會需要的chart資料

    onMounted(() => {
      console.log("show", grid);
      store.dispatch("cancel_edit_data");
      items.arr = store.state.box_item.map((item) => item);
      chart_item.arr = store.state.chart_data.map((item) => item);
      init();
    });

    const init = () => {
      count = 0;

      grid = GridStack.init({
        float: true,
        minRow: 1,
        minWidth: 500,
        column: 12,
        cellHeight: "80px",
      });

      // 有移動box就存一下
      grid.on("dragstop", () => {
        // (event, element)
        // const node = element.gridstackNode;

        items.arr.forEach((box) => {
          const new_node = grid.engine.nodes.filter((item) => box.id === item.id)[0];
          box.x = new_node.x;
          box.y = new_node.y;
        });

        store.dispatch("sava_box_data", items.arr);
      });
      grid.removeAll(true);
      //把box渲染到畫面上
      items.arr.forEach((element) => {
        add_new_widget(element);
      });

      //修改
      document.querySelectorAll(".edit").forEach((element) => {
        element.addEventListener("click", function () {
          items.arr.forEach((element2) => {
            if (element2.id === Number(this.dataset.id)) {
              store.dispatch("new_edit_data", element2);
            }
          });
          router.push("/NewBox");
        });
      });
      // 刪除
      document.querySelectorAll(".delete").forEach((element) => {
        element.addEventListener("click", function () {
          let title = "";
          let items_index;
          items.arr.forEach((element2, index) => {
            if (element2.id === Number(this.dataset.id)) {
              title = element2.title;
              items_index = index;
            }
          });

          let ans = confirm(`DELETE "${title}" ?`);
          if (ans) {
            items.arr.splice(items_index, 1);
            store.dispatch("sava_box_data", items.arr);
            grid.removeAll(true);
            init();
          }
        });
      });
    };

    //產生chart
    let count = 0;
    const show_chart = (item) => {
      var ctx = document.querySelectorAll(".myChartStatistics")[count].getContext("2d");
      let config = chart_item.arr.filter((x) => x.name == item.chartData)[0];
      config.type = item.chart;
      new Chart(ctx, config);
      count++;
    };

    //產生box
    const add_new_widget = (item) => {
      const node = item;

      grid.addWidget(node);
      if (item.chart === "pie" || item.chart === "bar") {
        show_chart(item);
      }
    };

    return {
      chart_item,
      init,
    };
  },
};
</script>

<style>
@import "../../node_modules/gridstack/dist/gridstack.min.css";

.btn-primary {
  color: #fff;
  background-color: #007bff;
}

.btn {
  display: inline-block;
  padding: 0.375rem 0.75rem;
  line-height: 1.5;
  border-radius: 0.25rem;
}

a {
  text-decoration: none;
}

h1 {
  font-size: 2.5rem;
  margin-bottom: 0.5rem;
}
.showBox {
  position: relative;
}
.showBox .grid-stack {
  width: 900px;
  min-height: 80vh;
  margin: 20px auto 50px auto;
  border: 1px dotted gray;
}

.grid-stack-item-content {
  background-color: #e5e5e5;
  border-radius: 8px;
}

.grid-stack-item-removing {
  opacity: 0.5;
}
.trash {
  height: 100px;
  background: rgba(255, 0, 0, 0.1) center center
    url(data:image/svg+xml;utf8;base64,)
    no-repeat;
}
.sidebar {
  background: rgba(0, 255, 0, 0.1);
  padding: 25px 0;
  height: 100px;
  text-align: center;
}
.sidebar .grid-stack-item {
  width: 120px;
  height: 50px;
  border: 2px dashed green;
  text-align: center;
  line-height: 35px;
  z-index: 10;
  background: rgba(0, 255, 0, 0.1);
  cursor: default;
  display: inline-block;
}
.sidebar .grid-stack-item .grid-stack-item-content {
  background: none;
}

.grid-stack .title {
  font-weight: bolder;
  background-color: rgb(202, 202, 202);
  position: sticky;
  top: 0;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}
.card table {
  margin: 20px auto;
  border-collapse: collapse;
  width: 90%;
}
.card table,
.card tr,
.card td {
  border-bottom: 1px solid gray;
  text-align: center;
}
.card td {
  padding: 5px;
}
.showBox .edit {
  width: 50px;
  position: absolute;
  bottom: 8px;
  right: 6px;
  background-color: rgb(46, 46, 46);
  padding: 4px 6px;
  border-radius: 4px;
  color: white;
  cursor: pointer;
  text-align: center;
  display: none;
}
.showBox .grid-stack-item-content:hover .edit {
  display: block;
}

.showBox .delete {
  width: 50px;
  position: absolute;
  bottom: 40px;

  right: 6px;
  background-color: rgb(137 42 42);
  padding: 4px 6px;
  border-radius: 4px;
  color: white;
  cursor: pointer;
  display: none;
}
.showBox .grid-stack-item-content:hover .delete {
  display: block;
}
</style>
